.right-pane {
  justify-self: center;
  align-self: center; }
  .right-pane .controls .control-label {
    display: inline-block;
    margin: 10px;
    padding-right: 10px; }
  .right-pane .controls .slider {
    margin: 0;
    padding-top: 15px; }
  .right-pane .subtitle {
    padding: 30px 0 10px 0; }
  .right-pane .code-copy-wrapper {
    position: relative; }
    .right-pane .code-copy-wrapper .code-wrapper {
      color: #282923;
      background-color: #F8F8F2;
      padding: 15px 20px;
      border-radius: 5px;
      margin-bottom: 20px;
      border: 1px solid rgba(0, 0, 0, 0.2);
      position: relative; }
      .right-pane .code-copy-wrapper .code-wrapper .variable {
        color: #FD9622; }
      .right-pane .code-copy-wrapper .code-wrapper .operator {
        color: #F92472; }
      .right-pane .code-copy-wrapper .code-wrapper .icon-copy {
        position: absolute;
        right: 7px;
        top: 0;
        bottom: 0;
        margin: auto;
        width: 35px;
        height: 35px; }
    .right-pane .code-copy-wrapper .tooltip-copy {
      position: absolute;
      top: 0;
      bottom: 0;
      right: -20px;
      transform: translateX(100%);
      height: 37px;
      margin: auto;
      padding: 5px 10px;
      border-radius: 5px;
      background-color: #282923;
      color: #F8F8F2; }
      .right-pane .code-copy-wrapper .tooltip-copy:before {
        content: "";
        display: inline-block;
        background-color: #282923;
        position: absolute;
        margin: auto;
        height: 5px;
        top: 0;
        bottom: 0;
        left: -7px;
        border-right: 7px solid #282923;
        border-top: 5px solid;
        border-bottom: 5px solid; }
