.left-pane {
  text-align: right;
  align-self: center;
  margin-left: auto;
  -webkit-user-select: none;
  /* Safari 3.1+ */
  -moz-user-select: none;
  /* Firefox 2+ */
  -ms-user-select: none;
  /* IE 10+ */
  user-select: none;
  /* Standard syntax */ }
  .left-pane .drawpad-wrapper {
    display: inline-block;
    text-align: center; }
    .left-pane .drawpad-wrapper .drawpad {
      position: relative;
      margin: 20px; }
      .left-pane .drawpad-wrapper .drawpad #scale {
        border: 1px solid transparent;
        pointer-events: none;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0; }
      .left-pane .drawpad-wrapper .drawpad #result {
        border: 1px solid transparent;
        pointer-events: none;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0; }
      .left-pane .drawpad-wrapper .drawpad #paint {
        border: 1px solid #00000033;
        display: block;
        margin: 0 auto; }
      .left-pane .drawpad-wrapper .drawpad Button {
        margin: 0 10px; }
    .left-pane .drawpad-wrapper .tools {
      margin: 10px 20px;
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      height: 36px; }
      .left-pane .drawpad-wrapper .tools .auto-clear-wrapper {
        margin: 0; }
